.App {
  text-align: center;
}

.App-logo {
 
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 4s linear;
  }
}
/*navbar 커스텀*/
.navbar{
  background-color:#011627 !important;
 }
.navLink{
  font-size:13px;
}
/*navbar 커스텀 종료*/
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
